/**
 * meta 
 * title: String
 * isMustWeiXin:true Boolean
 * isCheckAppid:appid,,true Boolean
 * menushow:,false
 * mpmenushow:,false
 * auth
 */
import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'
Vue.use(Router)

export default new Router({
	base: __dirname,
	mode: 'history',
	routes: [{
			path: '',
			redirect: '/center'
		},
		{
			path: '/test',
			name: 'test',
			component: resolve => require(['@/view/groupbuy/Test'], resolve),
			meta: {
				title: '',
				// isMustAuth: true,
				isCheckAppid: false
			}
		},
		//--------  start----------------//
		{
			path: '/index',
			name: 'index',
			component: resolve => require(['@/view/groupbuy/Buy'], resolve),
			meta: {
				title: '',
				// menushow: true,
				// isCheckAppid: false
			}
		},
	],
	scrollBehavior(to, from, savedPosition) {
		if(savedPosition) {
			return savedPosition
		} else {
			if(from.meta.keepAlive) {
				from.meta.savedPosition = document.body.scrollTop;
			}
			return {
				x: 0,
				y: to.meta.savedPosition || 0
			}
		}
	}
})



